<template>
    <div class="section-continer">
        <h2>Menu </h2>
        <div class="slider-wrap">
            <swiper 
            :autoplay="{
                delay: 2500,
                disableOnInteraction: false,
            }"
            :pagination="{
                clickable: true,
            }" 
            :navigation="true" 
            :modules="modules" 
            class="mySwiper"
            >
                <swiper-slide>
                    <img src="../assets/new-s-1.jpg">
                </swiper-slide>
                <swiper-slide>
                    <img src="../assets/new-s-2.jpg">
                </swiper-slide>
                <swiper-slide>
                    <img src="../assets/new-s-3.jpg">
                </swiper-slide>
                <swiper-slide>
                    <img src="../assets/new-s-4.jpg">
                </swiper-slide>
                <swiper-slide>
                    <img src="../assets/new-s-5.jpg">
                </swiper-slide>
                <swiper-slide>
                    <img src="../assets/new-s-6.jpg">
                </swiper-slide>
            </swiper>
        </div>
        <div class="menu-banner">
            <img src="../assets/new-s-5.jpg" alt="menu">
        </div>
        <div class="menu-container">
            <!--   -->
            <div class="nav-menu">
                <button class="btn" @click="changeComponent('CoffeeView')">Coffee</button>
                <button class="btn" @click="changeComponent('DrinkView')">Drink</button>
                <button class="btn" @click="changeComponent('TeaView')">Tea</button>
                <button class="btn" @click="changeComponent('FoodView')">Food</button>
            </div>
            <div class="menu-conent">
                <!--    -->
                <component v-bind:is="comp"></component>
            </div>
        </div>
    </div>
</template>

<script>
import $ from 'jquery';
import CoffeeView from './CoffeeView.vue';
import DrinkView from './DrinkView.vue';
import TeaView from './TeaView.vue';
import FoodView from './FoodView.vue';
// Import Swiper Vue.js components
import { Swiper, SwiperSlide } from 'swiper/vue';

// Import Swiper styles
import 'swiper/css';

import 'swiper/css/navigation';
import 'swiper/css/pagination';

import '../style.css';

// import required modules
import { Autoplay, Pagination, Navigation } from 'swiper/modules';

export default{
    components:{
        CoffeeView,
        DrinkView,
        TeaView,
        FoodView,
        Swiper,
        SwiperSlide,
    },
    setup() {
      return {
        modules: [Autoplay, Pagination,Navigation],
      };
    },
    data(){
        return{
            comp:'CoffeeView'
        }
    },
    methods:{
        changeComponent:function(componentName){
            this.comp=componentName;
        }
    },
    mounted(){
        $(".nav-menu button").eq(0).addClass("active");
        $(".nav-menu button").click(function(){
            $(".nav-menu button").removeClass("active")
            $(this).addClass("active")
        })
    }
}
</script>

<style>
.slider-wrap{
    width:100%;
    margin:20px 0;
}
.section-container{
    width:100%;
    max-width:1280px;
    border-top:1px dahsed black;
    border-bottom:1px dashed black;
    padding:50px 0;
    margin:auto;
}
.menu-banner{
    width:100%;
    border:1px solid black;
}
.menu-banner img{
    width:100%;
}
.menu-container{
    width:100%;
    display: flex;
    flex-wrap:wrap;
    border:1px solid black;
}
.nav-menu{
    width:20%;
    background-color:white;
    display:flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}
.nav-menu button{
    width:100%;
    padding:20px;
    background:skyblue;
    border:none;
    color:white;

}
.nav-menu button.active{
    background:#666;
}
.menu-content{
    background-color:#eee;
}
</style>